import axios from "axios"
import { useParams, useHistory } from "react-router-dom"
import { useState, useEffect } from "react"
import styled from "styled-components"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronLeft } from "@fortawesome/free-solid-svg-icons"

const Editor: React.FC<{ token: any }> = ({ token }) => {
  interface RouteParams {
    id: string
  }

  //
  const history = useHistory()
  const { id } = useParams<RouteParams>()

  //URL
  const url =
    process.env.NODE_ENV === "production"
      ? `/notebook/one/${id}`
      : `http://localhost:5000/notebook/one/${id}`
  const updateUrl =
    process.env.NODE_ENV === "production"
      ? "/notebook/update"
      : "http://localhost:5000/notebook/update"

  //State
  const [notebook, setNotebook] = useState<{
    id: string
    name: string
    content: string
  }>({
    id: "",
    name: "",
    content: "",
  })

  const [newContent, setNewContent] = useState(notebook.content)

  //Component did Mount
  useEffect(() => {
    ;(async () => {
      try {
        const res = await axios.get(url, {
          headers: { authToken: token },
        })
        setNotebook(res.data[0])
      } catch (err) {
        console.log(err)
      }
    })()
  }, [url, token, setNotebook])

  //Handlers
  const changeHandler = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setNewContent(e.target.value)
  }

  const save = async () => {
    try {
      const res = await axios.put(
        updateUrl,
        {
          name: null,
          content: newContent,
          notebook: notebook.id,
        },
        {
          headers: { authToken: token },
        }
      )
      console.log(res.data)
    } catch (err) {
      console.log(err)
    }
  }

  const back = () => {
    history.push("/dashboard")
  }

  return (
    <StyledEditor>
      <header>
        <h1 onClick={back}>
          <FontAwesomeIcon icon={faChevronLeft} />
          <span> Editor</span>
        </h1>
      </header>
      <div className="nav">
        <h3>{notebook.name}</h3>
        <h4 className="save" onClick={save}>
          Save
        </h4>
      </div>
      <textarea
        name="content"
        defaultValue={notebook.content}
        onChange={changeHandler}
      ></textarea>
      <footer>Articulator</footer>
    </StyledEditor>
  )
}

const StyledEditor = styled.div`
  width: 100vw;
  height: 100vh;
  background: #2c2c2c;
  display: flex;
  flex-direction: column;
  color: #dcdcdc;
  h1 {
    padding: 0rem 1rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    span {
      margin-left: 0.5em;
    }
    svg {
      font-size: 0.7em;
    }
  }
  .nav {
    padding: 0.5rem 1rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .save {
      cursor: pointer;
      text-decoration: underline;
      color: #fdfb94;
    }
  }
  textarea {
    width: 100%;
    height: 100%;
    background: #6e6e6e;
    border: 0;
    &:hover,
    &:focus {
      outline: 0;
    }
  }
`

export default Editor
