import { useState, useEffect } from "react";
import styled from "styled-components";
import axios from "axios";
import { StyledForm, StyledLogin } from "../../Style";
import { _escapeString } from "../../Utils";
import Nav from "./Nav";
import Background from "./Background";

const SignUp = () => {
  //URL
  const url =
    process.env.NODE_ENV === "production"
      ? "/user/signup"
      : "http://localhost:5000/user/signup";

  //State
  const [input, setInput] = useState<{
    email: string;
    password: string;
    name: string;
  }>({
    name: "",
    email: "",
    password: "",
  });
  const [animate, setAnimate] = useState(false);
  const [message, setMessage] = useState("");

  //Effects
  useEffect(() => {
    setAnimate(true);
    return () => setAnimate(false);
  }, []);

  //Handlers
  const changeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInput({ ...input, [e.target.name]: _escapeString(e.target.value) });
  };

  const submitHandler = async (e: React.FormEvent<HTMLDivElement>) => {
    e.preventDefault();
    try {
      const res = await axios.post(url, {
        email: input.email,
        name: input.name,
        password: input.password,
      });
      console.log(res.data);
    } catch (err) {
      console.log(err.response.data, err.response.status);
      setMessage(err.response.data.toString());
    } finally {
      setInput({ email: "", password: "", name: "" });
    }
  };

  //Variants
  const variants = {
    visible: {
      clipPath: "polygon(0 0, 100% 0, 100% 100%, 0 100%)",
    },
    hidden: {
      clipPath: "polygon(0 0, 100% 0, 100% 0, 0 0)",
    },
  };

  return (
    <StyledDiv>
      <Nav />
      <Background />
      <main>
        <StyledLogin
          initial="hidden"
          animate={animate ? "visible" : "hidden"}
          variants={variants}
          transition={{ duration: 0.5, ease: "easeOut" }}
          onSubmit={submitHandler}
        >
          <h3>Sign Up</h3>
          <StyledForm>
            <div className="fieldContainer">
              <p>{message}</p>
              <label htmlFor="email">Email:</label>
              <input
                type="email"
                name="email"
                value={input.email}
                required
                autoFocus
                onChange={changeHandler}
              />
            </div>
            <div className="fieldContainer">
              <label htmlFor="name">Name:</label>
              <input
                type="text"
                name="name"
                required
                onChange={changeHandler}
                value={input.name}
              />
            </div>
            <div className="fieldContainer">
              <label htmlFor="">Password:</label>
              <input
                type="password"
                name="password"
                required
                onChange={changeHandler}
                value={input.password}
              />
            </div>
            <button>Sign Up!!!</button>
          </StyledForm>
        </StyledLogin>
      </main>
    </StyledDiv>
  );
};

const StyledDiv = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  main {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;
export default SignUp;
